/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem 0;
  margin-bottom: 2.5rem 0;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-negro {
  background-color: #000000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border: none;
  text-transform: uppercase;
  border-radius: 1rem;
}
.boton-negro:hover {
  background-color: black;
  cursor: pointer;
}
.boton-negro:hover {
  color: #FF5757;
}

.boton-principal {
  background-color: #FF5757;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem 0;
  display: inline-block;
  border: none;
}
.boton-principal:hover {
  background-color: #ff3e3e;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/** UI **/
.header {
  background-color: #000000;
}
.header .barra a {
  font-weight: 700;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}
.contenido-header #nombre_usuario {
  color: #FFFFFF;
}

.header_interno {
  display: none;
}
@media (min-width: 768px) {
  .header_interno {
    display: inline;
  }
}

.header_movil {
  display: block;
}
.header_movil .btntema {
  height: 20px;
  filter: invert(100%);
}
.header_movil .btntema:hover {
  cursor: pointer;
}
.header_movil img {
  height: 60px;
}
@media (min-width: 768px) {
  .header_movil {
    display: none;
  }
}

.logo_movil {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.menu-fixed {
  position: fixed;
  z-index: 1000;
  top: 0;
  width: 100%;
  max-width: 1000px;
  box-shadow: 0px 4px 3px rgb(0, 0, 1);
}

.footer {
  background-color: #000000;
}
.footer .contenedor-footer {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 2.5rem;
  align-items: center;
  margin-top: 2rem;
  padding: 1rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer p {
  font-size: 1.8rem;
}
.footer img {
  width: 3.5rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    margin-right: 5rem;
  }
}
.derecha .dark-mode-boton {
  width: 2.5rem;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-right: 2rem;
    width: 35px;
    height: 35px;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 0rem;
  }
}
.barra .imgloco {
  width: 80px;
  height: 80px;
}
@media (min-width: 768px) {
  .barra .imgloco {
    width: 85px;
    height: 85px;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    opacity: 1;
    visibility: visible;
    justify-content: center;
    gap: 1rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #FF5757;
}

.body-index {
  font-family: "Overpass", sans-serif;
  font-weight: normal;
  font-size: 100%;
  color: #1b262c;
  margin: 0;
  background-color: #1a1a1a;
}

#contenedor {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  min-width: 100vw;
  min-height: 90vh;
  width: 100%;
  height: 90%;
}

#central {
  max-width: 320px;
  width: 100%;
}

.titulo {
  background-color: black;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 2rem;
  width: 10rem;
  height: 10rem;
}

.centrar_icono {
  display: flex;
  justify-content: center;
}

#login {
  width: 100%;
  padding: 50px 30px;
  background-color: #FF5757;
  -webkit-box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.15);
  border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  box-sizing: border-box;
}

#login input {
  font-family: "Overpass", sans-serif;
  font-size: 110%;
  color: #1b262c;
  display: block;
  width: 100%;
  height: 40px;
  margin-bottom: 10px;
  padding: 5px 5px 5px 10px;
  box-sizing: border-box;
  border: none;
  border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
}

#login input::placeholder {
  font-family: "Overpass", sans-serif;
  color: #000000;
  font-weight: 900;
}

#login button {
  font-family: "Overpass", sans-serif;
  font-size: 150%;
  font-weight: 700;
  color: #000000;
  width: 100%;
  height: 40px;
  border: none;
  color: #FFFFFF;
  border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  background-color: #000000;
  margin-top: 10px;
}

#login button:hover {
  color: #FF5757;
  cursor: pointer;
}

.pie-form {
  font-size: 90%;
  text-align: center;
  margin-top: 15px;
}

.pie-form a {
  display: block;
  text-decoration: none;
  color: #bbe1fa;
  margin-bottom: 3px;
}

.pie-form a:hover {
  color: #0f4c75;
}

.inferior {
  margin-top: 10px;
  font-size: 90%;
  text-align: center;
}

.inferior a {
  display: block;
  text-decoration: none;
  color: #bbe1fa;
  margin-bottom: 3px;
}

.inferior a:hover {
  color: #3282b8;
}